header {
  text-align: center;
}
@media screen and (max-width: 327px) {
  .budget-button {
    margin-top: 10px;
  }
}
@media screen and (max-width: 400px) {
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0px;
  }
  footer ul {
    margin-right: 0px;
  }
}

.dribbble-icon:hover {
  color: #ea4c89;
}
.twitter-icon:hover {
  color: #1da1f2;
}
.github-icon:hover {
  color: #171515;
}

.modalOverlay {
  position: absolute;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  min-height: 100%;
  top: 0;
}

.modalBack {
  background-color: white;
  padding-bottom: 30px;
  width: 80%;
  border-radius: 10px;
  border: solid 1px rgba(0, 0, 0, 0.7);
  margin: 250px auto;
  z-index: 2;
}

@media screen and (max-width: 575px) {
  .modalOverlay {
    height: 145%;
  }
}
@media screen and (max-width: 400px) {
  .modalOverlay {
    height: 148%;
  }
}
@media screen and (max-width: 350px) {
  .modalOverlay {
    height: 150%;
  }
}
@media screen and (max-width: 320px) {
  .modalOverlay {
    height: 155%;
  }
}
@media screen and (max-width: 290px) {
  .modalOverlay {
    height: 200%;
  }
}
